import styled from 'styled-components';


const Wrapper = styled.div`
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  max-width: 760px;
  padding: 15px;
  border-radius: 2px;
  background: transparent;
`;

export default Wrapper;


